.navbar {
  background: black;
  color: white;
  height: 60px; }
  .navbar ul {
    padding: 0;
    margin: 0;
    list-style: none; }
    .navbar ul li {
      display: inline-block;
      margin-left: 30px;
      text-align: center; }
      .navbar ul li a {
        display: block;
        color: white;
        line-height: 60px;
        width: 150px; }
        .navbar ul li a:hover {
          background: white;
          color: black; }
